input[type="radio"] {
    display: none;
}

input[type="radio"]+label {
    background-image: url('client/App/img/icon/radioFalse.png');
    background-position: left;
    background-repeat: no-repeat;
    padding-left: 30px !important;
    height: 24px;
    width: auto;
    display: inline-block;
    padding: 0 0 0 0px;
    cursor: pointer;
}

input[type="radio"]:checked+label {
    background-image: url('client/App/img/icon/radioTrue.png');
}

input[type="radio"].squared+label {
    background-image: url('client/App/img/icon/checkboxFalse.png');
    background-position: left;
    background-repeat: no-repeat;
    padding-left: 24px !important;
    padding-top: 3px !important;
    height: 24px;
    width: auto;
    display: inline-block;
    cursor: pointer;
}

input[type="radio"].squared:checked+label {
    background-image: url('client/App/img/icon/checkboxTrue.png');
}

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"]+label {
    background-image: url('client/App/img/icon/checkboxFalse.png');
    background-position: left;
    background-repeat: no-repeat;
    padding-left: 30px !important;
    height: 24px;
    width: auto;
    display: inline-block;
    padding: 0 0 0 0px;
    cursor: pointer;
}

input[type="checkbox"]:checked+label {
    background-image: url('client/App/img/icon/checkboxTrue.png');
}

.switch {
    position: relative;
    display: inline-block;
    width: 32px;
    height: 18px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 10px;
    width: 10px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: #8891F0;
}

input:checked+.slider.orange {
    background-color: #ffa800;
}


input:focus+.slider {
    box-shadow: 0 0 1px #8891F0;
}

input:focus+.slider.orange {
    box-shadow: 0 0 1px #ffa800;
}

input:checked+.slider {
    background-color: #8891F0;
}

input:checked+.slider.green {
    background-color: #22C993;
}


input:focus+.slider {
    box-shadow: 0 0 1px #8891F0;
}

input:focus+.slider.green {
    box-shadow: 0 0 1px #22C993;
}

input:checked+.slider:before {
    -webkit-transform: translateX(14px);
    -ms-transform: translateX(14px);
    transform: translateX(14px);
}

.slider.round {
    border-radius: 18px;
}

.slider.round:before {
    border-radius: 50%;
}

select::-ms-expand {
    display: none;
}

